import React, {useState, useEffect} from 'react';
import ContainerHeader from 'components/ContainerHeader/index';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import IntlMessages from 'util/IntlMessages';
import CardBox from 'components/CardBox/index';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import MenuItem from '@material-ui/core/MenuItem';
import {base_url, public_url, $http} from "config";
import AttachFile from '@material-ui/icons/AttachFile';
import {useHistory} from 'react-router-dom';
import Fab from '@material-ui/core/Fab';

const AddNew = (props) => {

    const sticker_id = props.location.state ? props.location.state.id : null;
    const [sticker_name, setStickerName] = useState("");
    const [category, setCategory] = useState(0);
    const [upload_img, setUploadImg] = useState(null);
    const [ImageUrl, setImageUrl] = useState('https://via.placeholder.com/300x300');
    const [status, setStatus] = useState(1);
    const [category_rows, setCategoryRows] = useState([]);
    const status_rows = ([
        {id: 1, content: 'Active'},
        {id: 0, content: 'Deactive'}
    ]);
    const History = useHistory();

    useEffect(() => {
        $http.get(base_url + "category/categoryList", {params: {id: sticker_id}})
        .then((response) => {
            setCategoryRows(response.data.category);
            if (sticker_id > 0) {
                setCategory(response.data.project.category_id);
                setStatus(response.data.project.status);
                setStickerName(response.data.project.name);
            }
        })
    }, [sticker_id]);

    const SaveSticker = () => {
        const frmData = new FormData();
        frmData.append('id', sticker_id);
        frmData.append('name', sticker_name);
        frmData.append('category_id', category);
        frmData.append('stiker_img', upload_img);
        frmData.append('status', status);

        $http.post(base_url + "sticker/updateSticker", frmData)
        .then((response) => {
            console.log(response.data);
            if (response.data.status === "success") {
                // NotificationManager.success(response.data.msg, "Success!", 5000, () => {
                // });
                History.push({
                    pathname: '/app/stickers/Categories',
                    state: {id: response.data.id}
                })
            } else if (response.data.status === "error") {
                NotificationManager.error(response.data.msg, "Error!", 5000, () => {
                });
            }
        }).catch((error)=>{
            console.log("error:", error);
        })

    }
    const changeImage = (e) =>{
        e.preventDefault();
    
        let reader = new FileReader();
        let file = e.target.files[0];
        reader.onloadend = () => {
            setUploadImg(file);
            setImageUrl(reader.result);
        }
        reader.readAsDataURL(file)
    }
    
    return (
        <div className="app-wrapper">
            <ContainerHeader match={props.match} title={<IntlMessages id="sidebar.stickers.addnew"/>}/>
            <div className="row">
              <div className="col-xl-3 col-lg-5 col-md-6 col-12">
                <div className="jr-card pb-2">
                    <div className="jr-card-thumb">
                        <img className="card-img-top img-fluid" alt="products" src={ImageUrl}/>
                    </div>
                    <input type="file" id="add_img" accept=".png" 
                        name="add_img" style={{display: "none"}}
                        onChange={(event) => changeImage(event)}/>

                    <div className="jr-card-social text-right">
                        <Fab className="jr-fab-btn bg-secondary text-white jr-btn-fab-xs mb-3" 
                            onClick={(e)=>{document.getElementById('add_img').click()}}>
                            <i className="zmdi zmdi-cloud-upload zmdi-hc-1x"/>
                        </Fab>
                    </div>
                </div>
              </div>
                <CardBox styleName="col-xl-9 col-lg-7 col-md-6 col-12">
                    <form className="row" autoComplete="off">
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="sticker_name"
                                label="Name"
                                value={sticker_name}
                                onChange={(event) => setStickerName(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Name"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField
                                id="category"
                                select
                                label="Category"
                                value={category}
                                onChange={(event) => setCategory(event.target.value)}
                                SelectProps={{}}
                                helperText="Please select Category"
                                margin="normal"
                                fullWidth
                            >
                                {category_rows.map(option => (
                                    <MenuItem key={option.id} value={option.id}>
                                        {option.name}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField
                                id="status"
                                select
                                label="Status"
                                value={status}
                                onChange={(event) => {setStatus(event.target.value)}}
                                SelectProps={{}}
                                helperText="Please select Status"
                                margin="normal"
                                fullWidth
                            >
                                {status_rows.map(option => (
                                    <MenuItem key={option.id} value={option.id}>
                                        {option.content}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </div>
                        <div className="col-md-12 col-12 text-right">
                            <Button variant="contained" color="primary" className="jr-btn" 
                                onClick={() => SaveSticker()}>
                                Save
                            </Button>
                        </div>
                    </form>
                </CardBox>
            </div>
            <NotificationContainer/>
        </div>
    );
}

export default AddNew;